<template>
	<div class="loader-bg" v-if="store.load">
		<span class="loader loader-chart"></span>
	</div>
	<div
		id="container-event"
		class="w-full min-h-[calc(100vh-180px)] flex items-center justify-center"
		v-if="store.hasConnection"
	>
		<!-- v-if="store.inWhiteList()" -->
		<div class="text-center mt-[-20vh] md:mt-0">
			<main class="parent-content-event">
				<div class="card lateral-l">
					<div class="content">
						<h2 class="title">Fragmentos Genesis</h2>
						<p class="copy">Evento del Sorteo al Mint Genesis</p>
						<!-- /img/galeria/img-5.png -->
						<img
							src="/img/topacio_azul.svg"
							alt=""
							id="logo-topacio"
							class="profile jv mini-logo"
						/>
						<button
							class="btn btn-sorteo-register"
							@click="store.participarEnSorteo()"
						>
							Participar en Sorteo
						</button>
						<p class="copy">{{ store.costoParticipacionSorteo }}</p>
					</div>
				</div>

