						@scroll="scrollController"
					>
						<li
							v-for="(winner, k) in store.hashesWinners.data"
							v-show="winner.img"
							:v-key="k"
							:id="`winner-${k}`"
						>
							<label class="text-nft">
								NFT ðŸ”–{{ winner.nftId }} -
								{{ formatWallet(winner.wallet) }}
								({{
									new Date(
										winner.timestamp * 1000,
									).toLocaleString()
								}})

								<p v-if="winner.attributes">
									{{ getAtributesFragment(winner) }}
								</p>
							</label>
							<a
								target="_blank"
								:href="`${store.getURLBlockchainScan()}/token/${store.getContractNFTS()}?a=${
									winner.nftId
								}`"
							>
								<img
									v-if="winner.img"
									:src="winner.img"
									alt="image nft"
								/>
							</a>
						</li>
					</ul>
				</div>
				<div class="lateral-r">
					<article>Resumen Diario</article>
					<div class="container">
						<ul>
							<li>
								Cantidad Participaciones:
								{{ cantidadParticipantes() }}
							</li>
							<li v-if="store.numeroSorteo > 0">
								Fragmentos Liberados:
								{{ store.numeroSorteo - 1 }}
							</li>
							<li v-if="store.proximoSorteo > 0">
								<label class="lbl-resumen subtitle"
									>Sorteo Fecha</label
								>
								<label class="lbl-resumen">
									{{
										new Date(
											store.proximoSorteo * 1000,
										).toLocaleString()
									}}
								</label>

								<CountDown
									:totalSegunds="cantidadSegundosSorteo"
									titleCustom="Tiempo para el sorteo"
								></CountDown>
							</li>
							<li class="contenedor-detalle-participantes">
								<label>Participantes</label>
								<label
									v-for="participante in store.sorteoParticipantsInDay"
									class="lbl-participante"
								>
									<img
										class="author-img avatar mini"
										:src="`https://avatars.dicebear.com/api/identicon/${participante}.svg?background=%23000`"
										alt="avatar"
									/>
									{{ formatWallet(participante) }}</label
								>
							</li>
						</ul>
					</div>
