		</div>
	</div>

	<NeedConnect v-else></NeedConnect>
</template>
<script>
import CountDown from "../components/CountDown.vue";
import NeedConnect from "../components/NeedConnect.vue";


import { mainStore } from "../store/mainStore";
import { onBeforeMount, onUnmounted, ref, onMounted, nextTick } from "vue";

export default {
	name: "EventPage",
	components: {
		CountDown,
		NeedConnect
	},
	setup() {
		const store = mainStore();
		const cantidadSegundosSorteo = ref(0);
		const isInit = ref(false);

		const cantidadParticipantes = () => {
			return store?.sorteoParticipantsInDay?.length
				? store.sorteoParticipantsInDay.length
				: 0;
		};

		const formatWallet = (w) => {
			return `${w.substr(0, 7)}  . . .  ${w.substr(-7, 7)}`;
		};

		const getAtributesFragment = (meta) => {
			if (!meta || !meta.attributes) {
				return ``;
			}

			const cadena = meta.attributes
				.flatMap((v, i) => `ðŸ”¹${v.trait_type}:${v.value}\n`)
				.toString()
				.replaceAll(",", "");

			return `${cadena}`;
		};

		// cada segundo actualiza el contador
		setInterval(() => {
			cantidadSegundosSorteo.value =
				cantidadSegundosSorteo.value > 0
					? cantidadSegundosSorteo.value - 1
					: 0;
		}, 1000);

		onUnmounted(() => {
			//console.log("onUnmounted");
			initData();
		});

		const initData = async () => {
			store.showLoader();

			store.hashesWinners.data = [];
			await nextTick();
			await store.searchWinners();
			const valorSegusdos =
				store && store.proximoSorteo
					? parseInt(
							(parseInt(store.proximoSorteo) * 1000 -
								new Date().getTime()) /
								1000,
					  )
					: 0;
			if (valorSegusdos > 0) {
				cantidadSegundosSorteo.value = valorSegusdos;
			}
			isInit.value = true;
			store.hideLoader();
		};

		onMounted(async () => {
			if (!isInit.value) {
				// Starting
				await initData();
				isInit.value = true;
			}
		});

		onBeforeMount(async () => {
			//console.log('onBeforeMount',isInit.value,cantidadSegundosSorteo.value);
			if (!isInit.value) {
				// Starting
				setTimeout(initData, 1000);
				isInit.value = true;
				//console.log('await initData',isInit.value)
			}
			// store.hashesWinners.data = [];
			// store.showLoader();
			// await nextTick();
			// const w = await store.searchWinners();
			// console.log({w})
		});
		const scrollController = ({
			target: { scrollTop, clientHeight, scrollHeight, lastElementChild },
		}) => {
			console.log(
				{
					scrollTop,
					clientHeight,
					scrollHeight,
					more: scrollTop + clientHeight >= scrollHeight,
					more2: scrollHeight - clientHeight <= (scrollTop+100),
					lastElementChild,

					// offsetTop: target.offsetTop,
					// offsetWidth: e.target.offsetWidth,
					// offsetHeight: e.target.offsetHeight,
					// offsetLeft: e.target.offsetLeft,
					// lastElementChild
				},
				"scrollController",
			);
		};
		return {
			store,
			cantidadParticipantes,
			formatWallet,
			cantidadSegundosSorteo,
			getAtributesFragment,
			isInit,
			initData,
			scrollController,
		};
	},
};
</script>

